@tailwind base;
@tailwind components;
@tailwind utilities;

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}

::-webkit-scrollbar {
  
    width: .4rem;
    height: .3rem;
    
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 3px rgb(210, 206, 206); 
    border-radius: .5rem;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #61b4f4; 
    border-radius: .6rem;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #4fa5f6; 

  }